import React, { useState, useRef, useCallback, useEffect } from 'react';
import { MessageCircle, Settings, Trash2, Star, Globe, Variable, GripVertical } from 'lucide-react';

// Mock types since we don't have access to the actual types
interface Node {
  id: string;
  type: string;
  position: { x: number; y: number };
  data?: {
    name?: string;
    type?: string;
    prompt?: string;
    firstMessage?: string;
    isStart?: boolean;
    variables?: any[];
    globalNodePlan?: { enabled: boolean };
  };
}

interface Edge {
  id: string;
  source: string;
  target: string;
  label?: string;
  type?: string;
}

interface ButtonProps {
  children: React.ReactNode;
  variant?: string;
  size?: string;
  className?: string;
  onClick?: (e: React.MouseEvent) => void;
  [key: string]: any;
}

interface BadgeProps {
  children: React.ReactNode;
  variant?: string;
  className?: string;
  [key: string]: any;
}

interface NodeComponentProps {
  data: any;
  selected: boolean;
  nodeId: string;
}

// Mock Button and Badge components
const Button: React.FC<ButtonProps> = ({ children, variant, size, className, onClick, ...props }) => (
  <button
    className={`inline-flex items-center justify-center font-medium transition-colors ${className || ''}`}
    onClick={onClick}
    {...props}
  >
    {children}
  </button>
);

const Badge: React.FC<BadgeProps> = ({ children, variant, className, ...props }) => (
  <span className={`inline-flex items-center px-2 py-1 rounded-full text-xs font-medium ${className || ''}`} {...props}>
    {children}
  </span>
);

// VapiNode component with fixed connection handling
const VapiNode: React.FC<NodeComponentProps> = ({ data, selected, nodeId }) => {
  const [isConfigOpen, setIsConfigOpen] = useState(false);
  const [localData, setLocalData] = useState(data);

  // Sync localData with incoming data prop changes (e.g., from sidebar updates)
  useEffect(() => {
    setLocalData(data);
  }, [data]);

  const handleUpdate = (updates: any) => {
    const newData = { ...localData, ...updates };
    setLocalData(newData);
    data.onUpdate(newData);
  };

  const getNodeIcon = () => {
    switch (data.type) {
      case 'conversation':
        return <MessageCircle className="h-4 w-4 text-blue-600" />;
      case 'apiRequest':
        return <div className="h-4 w-4 bg-purple-600 rounded" />;
      case 'transferCall':
        return <div className="h-4 w-4 bg-green-600 rounded" />;
      case 'endCall':
        return <div className="h-4 w-4 bg-red-600 rounded" />;
      case 'tool':
        return <div className="h-4 w-4 bg-orange-600 rounded" />;
      default:
        return <MessageCircle className="h-4 w-4 text-blue-600" />;
    }
  };

  const getNodeColor = () => {
    if (data.isStart) return 'border-green-500 bg-green-50';
    if (selected) return 'border-blue-500 shadow-lg';
    return 'border-gray-300 hover:border-gray-400';
  };

  return (
    <div
      className={`group relative w-[280px] bg-gradient-to-br from-white via-gray-50/30 to-white border-2 rounded-2xl shadow-lg transition-all duration-300 cursor-move hover:shadow-xl hover:shadow-teal-500/10 hover:scale-102 ${getNodeColor()}`}
      style={{
        filter: selected
          ? 'drop-shadow(0 12px 28px rgba(20, 184, 166, 0.2))'
          : 'drop-shadow(0 6px 16px rgba(0, 0, 0, 0.08))',
        backdropFilter: 'blur(8px)',
        background: selected
          ? 'linear-gradient(135deg, #ffffff 0%, #f0fdfa 30%, #ffffff 100%)'
          : 'linear-gradient(135deg, #ffffff 0%, #f8fafc 50%, #ffffff 100%)'
      }}
      draggable={false}
      onMouseDown={(e) => {
        const target = e.target as HTMLElement;
        if (target.closest('button') || target.closest('[role="button"]') || target.closest('[data-connection-point]')) {
          e.stopPropagation();
          e.preventDefault();
        }
      }}
      title="Click and drag to move this node"
    >
      {/* TOP CONNECTION POINT - Input */}
      <div
        className="absolute -top-1.5 left-1/2 transform -translate-x-1/2 w-3 h-3 bg-white border-2 border-teal-500 rounded-full cursor-crosshair transition-all duration-200 hover:scale-125 hover:border-teal-600 z-40"
        title="Input connection point - drag from another node's output to connect here"
        data-connection-point="input"
        data-node-id={nodeId}
        style={{
          boxShadow: '0 0 0 1px rgba(255, 255, 255, 0.9), 0 2px 4px rgba(20, 184, 166, 0.3)'
        }}
      />

      {/* Header */}
      <div className="p-3 border-b border-gray-100">
        <div className="flex items-center justify-between">
          <div className="flex items-center gap-2">
            <GripVertical className="h-4 w-4 text-gray-400 cursor-move" />
            <div className="w-8 h-8 bg-blue-100 rounded-lg flex items-center justify-center">
              {getNodeIcon()}
            </div>
            <div>
              <div className="text-sm font-medium text-gray-900">
                {localData.name || 'Conversation'}
              </div>
              <div className="flex items-center gap-1 mt-1">
                {localData.isStart && (
                  <Badge variant="secondary" className="text-xs bg-green-100 text-green-700">
                    <Star className="h-3 w-3 mr-1" />
                    Start
                  </Badge>
                )}
                {localData.globalNodePlan?.enabled && (
                  <Badge variant="outline" className="text-xs border-orange-200 text-orange-700">
                    <Globe className="h-3 w-3 mr-1" />
                    Global
                  </Badge>
                )}
                {localData.variables && localData.variables.length > 0 && (
                  <Badge variant="outline" className="text-xs border-purple-200 text-purple-700">
                    <Variable className="h-3 w-3 mr-1" />
                    {localData.variables.length}
                  </Badge>
                )}
              </div>
            </div>
          </div>
          <div className="flex items-center gap-1">
            <Button
              variant="ghost"
              size="sm"
              className="h-8 w-8 p-0 hover:bg-gray-100 rounded-md"
              onClick={(e: React.MouseEvent) => {
                e.stopPropagation();
                e.preventDefault();
                if (data.onConfigure) {
                  data.onConfigure();
                } else {
                  setIsConfigOpen(true);
                }
              }}
            >
              <Settings className="h-4 w-4 text-gray-500" />
            </Button>
            {!localData.isStart && (
              <Button
                variant="ghost"
                size="sm"
                className="h-8 w-8 p-0 hover:bg-red-100 text-red-500 hover:text-red-700 rounded-md"
                onClick={(e: React.MouseEvent) => {
                  e.stopPropagation();
                  e.preventDefault();
                  if (confirm('Are you sure you want to delete this node?')) {
                    data.onDelete();
                  }
                }}
              >
                <Trash2 className="h-4 w-4" />
              </Button>
            )}
          </div>
        </div>
      </div>

      {/* Content */}
      <div className="p-3 space-y-2">
        <div className="text-xs">
          <div className="font-medium text-gray-900 mb-1">Prompt</div>
          <div className="text-gray-600 leading-relaxed">
            {localData.prompt?.length > 80
              ? `${localData.prompt.substring(0, 80)}...`
              : localData.prompt || 'No prompt configured'}
          </div>
        </div>
      </div>

      {/* BOTTOM CONNECTION POINT - Output (for manual connections) */}
      {localData.type !== 'endCall' && (
        <div
          className="absolute -bottom-1.5 left-1/2 transform -translate-x-1/2 w-3 h-3 bg-white border-2 border-teal-500 rounded-full cursor-crosshair transition-all duration-200 hover:scale-125 hover:border-teal-600 z-40"
          title="Output connection point - drag to another node's input to connect"
          data-connection-point="output"
          data-node-id={nodeId}
          onMouseDown={(e) => {
            e.stopPropagation();
            e.preventDefault();
            console.log('ðŸ”— Starting manual connection from node:', nodeId);
            if (data.onManualConnectionStart && nodeId) {
              // Pass the exact position of this connection point
              const rect = e.currentTarget.getBoundingClientRect();
              const canvasRect = e.currentTarget.closest('[data-canvas]')?.getBoundingClientRect();
              if (canvasRect) {
                const relativeX = rect.left + rect.width / 2 - canvasRect.left;
                const relativeY = rect.top + rect.height / 2 - canvasRect.top;
                data.onManualConnectionStart(nodeId, { x: relativeX, y: relativeY });
              }
            }
          }}
          style={{
            boxShadow: '0 0 0 1px rgba(255, 255, 255, 0.9), 0 2px 4px rgba(20, 184, 166, 0.3)'
          }}
        />
      )}

      {/* ADD NODE BUTTON - For quick node creation - Only visible on hover */}
      {localData.type !== 'endCall' && (
        <div className="absolute -bottom-12 left-1/2 transform -translate-x-1/2 z-30 opacity-0 group-hover:opacity-100 transition-opacity duration-200">
          <button
            className="w-8 h-8 bg-teal-500 hover:bg-teal-600 text-white rounded-full flex items-center justify-center transition-all duration-200 shadow-lg hover:shadow-xl border-2 border-white hover:scale-110 active:scale-95"
            title="Add new connected node"
            onClick={(e) => {
              e.stopPropagation();
              console.log('âž• Quick add node for:', nodeId);
              if (data.onConnectionStart && nodeId) {
                data.onConnectionStart(nodeId, { x: 0, y: 0 });
              }
            }}
            style={{
              boxShadow: '0 0 0 2px rgba(255, 255, 255, 0.9), 0 4px 12px rgba(20, 184, 166, 0.3)'
            }}
          >
            <span className="text-lg font-bold leading-none">+</span>
          </button>
        </div>
      )}
    </div>
  );
};

// Main Canvas Component
interface VapiCanvasSimpleProps {
  nodes: Node[];
  edges: Edge[];
  onNodesChange: (nodes: Node[]) => void;
  onEdgesChange?: (edges: Edge[]) => void;
  onNodeClick: (node: Node) => void;
  selectedNode?: Node | null;
  nodeTypes: any;
  onEdgeLabelClick?: (edge: Edge) => void;
}

const VapiCanvasSimple: React.FC<VapiCanvasSimpleProps> = ({
  nodes,
  edges,
  onNodesChange,
  onEdgesChange,
  onNodeClick,
  selectedNode,
  nodeTypes,
  onEdgeLabelClick,
}) => {
  // Drag state
  const [isDragging, setIsDragging] = useState(false);
  const [draggedNodeId, setDraggedNodeId] = useState<string | null>(null);
  const [dragOffset, setDragOffset] = useState({ x: 0, y: 0 });
  const [forceRender, setForceRender] = useState(0);
  const [hasMoved, setHasMoved] = useState(false); // Track if mouse actually moved during drag

  // Zoom state
  const [zoom, setZoom] = useState(1);
  const [panOffset, setPanOffset] = useState({ x: 0, y: 0 });

  // Canvas panning state
  const [isPanning, setIsPanning] = useState(false);
  const [panStart, setPanStart] = useState({ x: 0, y: 0 });
  const [lastPanOffset, setLastPanOffset] = useState({ x: 0, y: 0 });

  // Manual connection state - FIXED
  const [isConnecting, setIsConnecting] = useState(false);
  const [connectionStart, setConnectionStart] = useState<{ nodeId: string; x: number; y: number } | null>(null);
  const [mousePosition, setMousePosition] = useState<{ x: number; y: number }>({ x: 0, y: 0 });

  const canvasRef = useRef<HTMLDivElement>(null);
  const nodesRef = useRef(nodes);
  const onNodesChangeRef = useRef(onNodesChange);
  const onNodeClickRef = useRef(onNodeClick);

  // Update refs when props change
  nodesRef.current = nodes;
  onNodesChangeRef.current = onNodesChange;
  onNodeClickRef.current = onNodeClick;

  // Zoom handler
  const handleWheel = useCallback((e: React.WheelEvent) => {
    e.preventDefault();

    const rect = canvasRef.current?.getBoundingClientRect();
    if (!rect) return;

    const mouseX = e.clientX - rect.left;
    const mouseY = e.clientY - rect.top;

    // Calculate zoom factor
    const zoomFactor = e.deltaY > 0 ? 0.9 : 1.1;
    const newZoom = Math.min(Math.max(zoom * zoomFactor, 0.1), 3);

    // Calculate new pan offset to zoom towards mouse position
    const zoomRatio = newZoom / zoom;
    const newPanX = mouseX - (mouseX - panOffset.x) * zoomRatio;
    const newPanY = mouseY - (mouseY - panOffset.y) * zoomRatio;

    setZoom(newZoom);
    setPanOffset({ x: newPanX, y: newPanY });
  }, [zoom, panOffset]);

  // Canvas panning handlers - VAPI style (simple click and drag)
  const handleCanvasMouseDown = useCallback((e: React.MouseEvent) => {
    // Only start panning if clicking on empty canvas (not on nodes or connections)
    const target = e.target as HTMLElement;
    if (target.closest('[data-node]') || target.closest('button') || target.closest('[data-connection-point]')) {
      return;
    }

    // Start panning with left mouse button on empty canvas (VAPI behavior)
    if (e.button === 0) {
      e.preventDefault();
      setIsPanning(true);
      setPanStart({ x: e.clientX, y: e.clientY });
      setLastPanOffset({ ...panOffset });
      document.body.style.cursor = 'grabbing';
    }
  }, [panOffset]);

  const handleCanvasPanMove = useCallback((e: React.MouseEvent) => {
    if (isPanning) {
      e.preventDefault();
      const deltaX = e.clientX - panStart.x;
      const deltaY = e.clientY - panStart.y;

      setPanOffset({
        x: lastPanOffset.x + deltaX,
        y: lastPanOffset.y + deltaY
      });
    }
  }, [isPanning, panStart, lastPanOffset]);

  const handleCanvasPanEnd = useCallback(() => {
    if (isPanning) {
      setIsPanning(false);
      document.body.style.cursor = 'default';
    }
  }, [isPanning]);

  // FIXED: Manual connection start handler
  const startManualConnection = useCallback((nodeId: string, position: { x: number; y: number }) => {
    console.log('ðŸ”— Starting manual connection from:', nodeId, 'at position:', position);

    const node = nodes.find(n => n.id === nodeId);
    if (!node) {
      console.error('âŒ Source node not found:', nodeId);
      return;
    }

    // Calculate the actual connection point position (in transformed space)
    const connectionX = node.position.x + 140; // Center of node (280px width / 2)
    const connectionY = node.position.y + 180; // Bottom of node (approximate height)

    setIsConnecting(true);
    setConnectionStart({
      nodeId,
      x: connectionX,
      y: connectionY
    });

    // Change cursor to indicate connection mode
    document.body.style.cursor = 'crosshair';
    console.log('âœ… Manual connection started from:', nodeId, { x: connectionX, y: connectionY });
  }, [nodes]);

  // Handle canvas mouse move for connection preview
  const handleCanvasMouseMove = useCallback((e: React.MouseEvent) => {
    if (isConnecting) {
      const rect = canvasRef.current?.getBoundingClientRect();
      if (rect) {
        // Account for zoom and pan when calculating mouse position
        const rawX = e.clientX - rect.left;
        const rawY = e.clientY - rect.top;
        const adjustedX = (rawX - panOffset.x) / zoom;
        const adjustedY = (rawY - panOffset.y) / zoom;

        setMousePosition({
          x: adjustedX,
          y: adjustedY
        });
      }
    }
  }, [isConnecting, zoom, panOffset]);

  // FIXED: Handle canvas mouse up for connection completion
  const handleCanvasMouseUp = useCallback((e: React.MouseEvent) => {
    if (!isConnecting || !connectionStart) return;

    const rect = canvasRef.current?.getBoundingClientRect();
    if (!rect) return;

    // Account for zoom and pan when calculating mouse position
    const rawX = e.clientX - rect.left;
    const rawY = e.clientY - rect.top;
    const mouseX = (rawX - panOffset.x) / zoom;
    const mouseY = (rawY - panOffset.y) / zoom;

    console.log('ðŸŽ¯ Trying to complete connection at:', { mouseX, mouseY });

    // Find target node under mouse
    const targetNode = nodes.find(node => {
      const nodeLeft = node.position.x;
      const nodeRight = node.position.x + 280; // Node width
      const nodeTop = node.position.y;
      const nodeBottom = node.position.y + 180; // Approximate node height

      const isInside = mouseX >= nodeLeft && mouseX <= nodeRight &&
                      mouseY >= nodeTop && mouseY <= nodeBottom &&
                      node.id !== connectionStart.nodeId; // Can't connect to self

      if (isInside) {
        console.log('ðŸŽ¯ Found target node:', node.id, {
          nodeBounds: { left: nodeLeft, right: nodeRight, top: nodeTop, bottom: nodeBottom },
          mousePos: { mouseX, mouseY }
        });
      }

      return isInside;
    });

    if (targetNode && onEdgesChange) {
      // Check if connection already exists
      const existingEdge = edges.find(edge => 
        edge.source === connectionStart.nodeId && edge.target === targetNode.id
      );

      if (existingEdge) {
        console.log('âš ï¸ Connection already exists between these nodes');
      } else {
        // Create new connection
        const newEdge: Edge = {
          id: `edge-${connectionStart.nodeId}-${targetNode.id}-${Date.now()}`,
          source: connectionStart.nodeId,
          target: targetNode.id,
          label: 'Manual Connection',
          type: 'default'
        };

        const updatedEdges = [...edges, newEdge];
        onEdgesChange(updatedEdges);

        console.log('âœ… Created manual connection:', connectionStart.nodeId, '->', targetNode.id);
      }
    } else {
      console.log('âŒ No valid target node found for connection');
    }

    // Reset connection state
    setIsConnecting(false);
    setConnectionStart(null);
    document.body.style.cursor = 'default';
  }, [isConnecting, connectionStart, nodes, edges, onEdgesChange]);

  // Auto add node functionality (existing)
  const handleAddNode = useCallback((sourceNodeId: string) => {
    console.log('âž• Adding new node after:', sourceNodeId);

    const sourceNode = nodes.find(n => n.id === sourceNodeId);
    if (!sourceNode) return;

    const newNodeId = `node-${Date.now()}`;
    const newNode: Node = {
      id: newNodeId,
      type: 'conversation',
      position: {
        x: sourceNode.position.x,
        y: sourceNode.position.y + 300
      },
      data: {
        type: 'conversation',
        name: 'New Conversation',
        prompt: 'Enter your prompt here...',
        firstMessage: '',
        isStart: false,
        variables: [],
        globalNodePlan: { enabled: false },
      },
    };

    const newEdge: Edge = {
      id: `edge-${sourceNodeId}-${newNodeId}`,
      source: sourceNodeId,
      target: newNodeId,
      label: 'Auto Connection'
    };

    const updatedNodes = [...nodes, newNode];
    const updatedEdges = [...edges, newEdge];

    onNodesChangeRef.current(updatedNodes);
    if (onEdgesChange) {
      onEdgesChange(updatedEdges);
    }
  }, [nodes, edges, onEdgesChange]);

  // Node drag handlers (existing)
  const handleNodeMouseDown = useCallback((e: React.MouseEvent, node: Node) => {
    const target = e.target as HTMLElement;
    if (target.closest('button') || target.closest('[role="button"]') || target.closest('[data-connection-point]')) {
      return;
    }

    e.preventDefault();
    e.stopPropagation();

    const rect = canvasRef.current?.getBoundingClientRect();
    if (!rect) return;

    // Account for zoom and pan when calculating mouse position
    const rawX = e.clientX - rect.left;
    const rawY = e.clientY - rect.top;
    const mouseX = (rawX - panOffset.x) / zoom;
    const mouseY = (rawY - panOffset.y) / zoom;

    const offset = {
      x: mouseX - node.position.x,
      y: mouseY - node.position.y,
    };

    setIsDragging(true);
    setDraggedNodeId(node.id);
    setDragOffset(offset);
    setHasMoved(false); // Reset movement tracking
  }, [zoom, panOffset]);

  const handleMouseMove = useCallback((e: React.MouseEvent) => {
    // Handle canvas panning
    handleCanvasPanMove(e);

    // Handle node dragging
    if (isDragging && draggedNodeId) {
      e.preventDefault();
      const rect = canvasRef.current?.getBoundingClientRect();
      if (!rect) return;

      // Account for zoom and pan when calculating mouse position
      const rawX = e.clientX - rect.left;
      const rawY = e.clientY - rect.top;
      const mouseX = (rawX - panOffset.x) / zoom;
      const mouseY = (rawY - panOffset.y) / zoom;

      const newX = mouseX - dragOffset.x;
      const newY = mouseY - dragOffset.y;

      // Check if the node actually moved (not just a click)
      const currentNode = nodesRef.current.find(n => n.id === draggedNodeId);
      if (currentNode) {
        const deltaX = Math.abs(newX - currentNode.position.x);
        const deltaY = Math.abs(newY - currentNode.position.y);
        if (deltaX > 2 || deltaY > 2) { // Threshold to distinguish drag from click
          setHasMoved(true);
        }
      }

      const updatedNodes = nodesRef.current.map(node =>
        node.id === draggedNodeId
          ? { ...node, position: { x: newX, y: newY } }
          : node
      );

      nodesRef.current = updatedNodes;
      setForceRender(prev => prev + 1);
      onNodesChangeRef.current(updatedNodes);
    }

    // Handle connection preview
    handleCanvasMouseMove(e);
  }, [isDragging, draggedNodeId, dragOffset, handleCanvasMouseMove, zoom, panOffset, handleCanvasPanMove]);

  const handleMouseUp = useCallback((e: React.MouseEvent) => {
    // Handle canvas panning end
    handleCanvasPanEnd();

    if (isDragging) {
      e.preventDefault();
      setIsDragging(false);
      setDraggedNodeId(null);
      setDragOffset({ x: 0, y: 0 });
      // Note: Don't reset hasMoved here, let handleNodeClick use it first
    }

    // Handle connection completion
    handleCanvasMouseUp(e);
  }, [isDragging, handleCanvasMouseUp, handleCanvasPanEnd]);

  const handleNodeClick = useCallback((node: Node) => {
    // Only open sidebar if the node wasn't dragged (just clicked)
    if (!hasMoved) {
      onNodeClickRef.current(node);
    }
    // Reset the movement tracking after handling the click
    setHasMoved(false);
  }, [hasMoved]);

  // Render connections
  const renderConnections = useCallback(() => {
    if (!edges || edges.length === 0) return null;

    return edges.map((edge, index) => {
      const currentNodes = nodesRef.current;
      const sourceNode = currentNodes.find(n => n.id === edge.source);
      const targetNode = currentNodes.find(n => n.id === edge.target);

      if (!sourceNode || !targetNode) return null;

      const sourceX = sourceNode.position.x + 140; // Center
      const sourceY = sourceNode.position.y + 160; // Bottom
      const targetX = targetNode.position.x + 140; // Center  
      const targetY = targetNode.position.y - 8; // Top

      const midX = sourceX + (targetX - sourceX) / 2;
      const midY = sourceY + (targetY - sourceY) / 2;

      const distance = Math.sqrt(
        Math.pow(targetX - sourceX, 2) + Math.pow(targetY - sourceY, 2)
      );
      const controlOffset = Math.min(distance * 0.4, 100);

      const cp1x = sourceX;
      const cp1y = sourceY + controlOffset;
      const cp2x = targetX;
      const cp2y = targetY - controlOffset;

      const pathData = `M ${sourceX} ${sourceY} C ${cp1x} ${cp1y}, ${cp2x} ${cp2y}, ${targetX} ${targetY}`;

      return (
        <g key={`edge-${index}`}>
          <defs>
            <marker
              id={`arrowhead-${index}`}
              markerWidth="8"
              markerHeight="6"
              refX="7"
              refY="3"
              orient="auto"
              markerUnits="strokeWidth"
            >
              <polygon points="0 0, 8 3, 0 6" fill="#6b7280" />
            </marker>
          </defs>

          <path
            d={pathData}
            stroke="#6b7280"
            strokeWidth="2"
            fill="none"
            strokeLinecap="round"
            markerEnd={`url(#arrowhead-${index})`}
            className="transition-all duration-300 hover:stroke-teal-500"
          />

          <foreignObject
            x={midX - 60}
            y={midY - 12}
            width="120"
            height="24"
            className="pointer-events-auto"
          >
            <div
              className="bg-white text-gray-800 text-xs font-medium px-3 py-1 rounded-full text-center shadow-md border border-gray-200 cursor-pointer hover:shadow-lg transition-shadow"
              onClick={(e) => {
                e.stopPropagation();
                if (onEdgeLabelClick) {
                  onEdgeLabelClick(edge);
                }
              }}
            >
              {edge.label || 'Connection'}
            </div>
          </foreignObject>
        </g>
      );
    });
  }, [edges, forceRender, onEdgeLabelClick]);

  return (
    <div className="relative w-full h-full bg-gradient-to-br from-slate-50 via-white to-gray-50 overflow-hidden">
      {/* Grid background */}
      <div
        className="absolute inset-0 opacity-[0.12]"
        style={{
          backgroundImage: `
            linear-gradient(rgba(99, 102, 241, 0.15) 1px, transparent 1px),
            linear-gradient(90deg, rgba(99, 102, 241, 0.15) 1px, transparent 1px)
          `,
          backgroundSize: '24px 24px'
        }}
      />

      <div
        ref={canvasRef}
        data-canvas="true"
        className="relative w-full h-full"
        onMouseDown={handleCanvasMouseDown}
        onMouseMove={handleMouseMove}
        onMouseUp={handleMouseUp}
        onMouseLeave={handleMouseUp}
        onWheel={handleWheel}
        style={{ cursor: isPanning ? 'grabbing' : 'grab' }}
      >
        {/* Workflow content container - Only this gets transformed */}
        <div
          className="absolute inset-0"
          style={{
            transform: `translate(${panOffset.x}px, ${panOffset.y}px) scale(${zoom})`,
            transformOrigin: '0 0',
          }}
        >
    
     

        {/* Render nodes */}
        {nodes.map((node) => (
          <div
            key={node.id}
            data-node="true"
            className="absolute"
            style={{
              left: node.position.x,
              top: node.position.y,
              zIndex: 10,
            }}
            onMouseDown={(e) => handleNodeMouseDown(e, node)}
            onClick={() => handleNodeClick(node)}
          >
            <VapiNode
              data={{
                name: node.data?.name || node.type,
                type: node.type,
                prompt: node.data?.prompt || '',
                firstMessage: node.data?.firstMessage || '',
                isStart: node.data?.isStart || false,
                variables: node.data?.variables || [],
                globalNodePlan: node.data?.globalNodePlan || { enabled: false },
                onUpdate: (data: any) => {
                  const updatedNodes = nodesRef.current.map(n =>
                    n.id === node.id ? { ...n, data: { ...n.data, ...data } } : n
                  );
                  onNodesChangeRef.current(updatedNodes);
                },
                onDelete: () => {
                  const updatedNodes = nodesRef.current.filter(n => n.id !== node.id);
                  const updatedEdges = edges.filter(e => e.source !== node.id && e.target !== node.id);
                  onNodesChangeRef.current(updatedNodes);
                  if (onEdgesChange) {
                    onEdgesChange(updatedEdges);
                  }
                },
                onConfigure: () => handleNodeClick(node),
                onConnectionStart: () => handleAddNode(node.id), // Quick add
                onManualConnectionStart: startManualConnection, // Manual connection
              }}
              selected={selectedNode?.id === node.id}
              nodeId={node.id}
            />
          </div>
        ))}

        {/* Connection SVG Layer */}
        <svg
          className="absolute inset-0 pointer-events-none"
          style={{ zIndex: 5 }}
          width="100%"
          height="100%"
        >
          {renderConnections()}

          {/* Manual connection preview line */}
          {isConnecting && connectionStart && (
            <g>
              <defs>
                <linearGradient id="connectionPreview" x1="0%" y1="0%" x2="100%" y2="0%">
                  <stop offset="0%" stopColor="#14b8a6" stopOpacity="1.0" />
                  <stop offset="100%" stopColor="#06b6d4" stopOpacity="0.8" />
                </linearGradient>
              </defs>

              <path
                d={`M ${connectionStart.x} ${connectionStart.y} Q ${connectionStart.x + (mousePosition.x - connectionStart.x) / 2} ${connectionStart.y + 50}, ${mousePosition.x} ${mousePosition.y}`}
                stroke="url(#connectionPreview)"
                strokeWidth="3"
                fill="none"
                strokeLinecap="round"
                strokeDasharray="8,4"
                className="animate-pulse"
              />

              <circle
                cx={mousePosition.x}
                cy={mousePosition.y}
                r="6"
                fill="#14b8a6"
                stroke="white"
                strokeWidth="2"
                className="animate-pulse"
              />
            </g>
          )}
        </svg>
        </div>

        {/* Canvas controls - VAPI style */}
        <div className="absolute bottom-4 right-4 bg-white rounded-lg shadow-sm border border-gray-200 z-50 flex items-center gap-1 px-2 py-1">
          <button
            className="w-7 h-7 hover:bg-gray-100 rounded flex items-center justify-center text-gray-600 hover:text-gray-800 transition-colors"
            onClick={() => {
              const newZoom = Math.max(zoom * 0.8, 0.1);
              setZoom(newZoom);
            }}
            title="Zoom Out"
          >
            <span className="text-sm font-bold leading-none">âˆ’</span>
          </button>
          <div className="text-xs text-gray-500 font-medium px-2 min-w-[40px] text-center">
            {Math.round(zoom * 100)}%
          </div>
          <button
            className="w-7 h-7 hover:bg-gray-100 rounded flex items-center justify-center text-gray-600 hover:text-gray-800 transition-colors"
            onClick={() => {
              const newZoom = Math.min(zoom * 1.2, 3);
              setZoom(newZoom);
            }}
            title="Zoom In"
          >
            <span className="text-sm font-bold leading-none">+</span>
          </button>
        </div>

        
       
      </div>
    </div>
  );
};
export default VapiCanvasSimple;
