import React, { useState, useEffect } from 'react';
import { X, Save } from 'lucide-react';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Textarea } from '@/components/ui/textarea';
import { Edge } from '../types/workflow';

interface EdgeLabelEditSidebarProps {
  isOpen: boolean;
  onClose: () => void;
  edge: Edge | null;
  onSave: (edgeId: string, newLabel: string) => void;
}

const EdgeLabelEditSidebar: React.FC<EdgeLabelEditSidebarProps> = ({
  isOpen,
  onClose,
  edge,
  onSave,
}) => {
  const [label, setLabel] = useState('');
  const [description, setDescription] = useState('');

  useEffect(() => {
    if (edge) {
      setLabel(edge.label || '');
      setDescription(''); // You can add description field to Edge type if needed
    }
  }, [edge]);

  const handleSave = () => {
    if (edge && label.trim()) {
      onSave(edge.id, label.trim());
      onClose();
    }
  };

  const handleKeyDown = (e: React.KeyboardEvent) => {
    if (e.key === 'Escape') {
      onClose();
    } else if (e.key === 'Enter' && (e.ctrlKey || e.metaKey)) {
      handleSave();
    }
  };

  if (!isOpen || !edge) {
    return null;
  }

  return (
    <>
      {/* Backdrop */}
      <div 
        className="fixed inset-0 bg-black bg-opacity-50 z-40"
        onClick={onClose}
      />
      
      {/* Sidebar */}
      <div 
        className="fixed right-0 top-0 h-full w-96 bg-white shadow-2xl z-50 transform transition-transform duration-300 ease-in-out"
        onKeyDown={handleKeyDown}
        tabIndex={-1}
      >
        {/* Header */}
        <div className="flex items-center justify-between p-6 border-b border-gray-200 bg-gradient-to-r from-teal-50 to-emerald-50">
          <div>
            <h2 className="text-xl font-semibold text-gray-900">Edit Connection Label</h2>
            <p className="text-sm text-gray-600 mt-1">Customize the condition text for this workflow connection</p>
          </div>
          <Button
            variant="ghost"
            size="sm"
            onClick={onClose}
            className="h-8 w-8 p-0 hover:bg-gray-100"
          >
            <X className="h-4 w-4" />
          </Button>
        </div>

        {/* Content */}
        <div className="p-6 space-y-6">
          {/* Connection Info */}
          <div className="bg-gray-50 rounded-lg p-4">
            <h3 className="text-sm font-medium text-gray-900 mb-2">Connection Details</h3>
            <div className="text-xs text-gray-600 space-y-1">
              <div>From: <span className="font-medium">{edge.source}</span></div>
              <div>To: <span className="font-medium">{edge.target}</span></div>
              <div>ID: <span className="font-mono">{edge.id}</span></div>
            </div>
          </div>

          {/* Label Input */}
          <div className="space-y-2">
            <Label htmlFor="edge-label" className="text-sm font-medium text-gray-900">
              Condition Label
            </Label>
            <Input
              id="edge-label"
              value={label}
              onChange={(e) => setLabel(e.target.value)}
              placeholder="e.g., if the user said yes"
              className="w-full"
              autoFocus
            />
            <p className="text-xs text-gray-500">
              This text will appear on the connection line between nodes
            </p>
          </div>

          {/* Description Input (Optional) */}
          <div className="space-y-2">
            <Label htmlFor="edge-description" className="text-sm font-medium text-gray-900">
              Description (Optional)
            </Label>
            <Textarea
              id="edge-description"
              value={description}
              onChange={(e) => setDescription(e.target.value)}
              placeholder="Add additional details about this condition..."
              className="w-full h-20 resize-none"
            />
            <p className="text-xs text-gray-500">
              Internal notes about this connection condition
            </p>
          </div>

          {/* Common Condition Templates */}
          <div className="space-y-2">
            <Label className="text-sm font-medium text-gray-900">
              Quick Templates
            </Label>
            <div className="grid grid-cols-1 gap-2">
              {[
                'if the user said yes',
                'if the user said no',
                'if user needs help',
                'if call should transfer',
                'if information collected',
                'default path'
              ].map((template) => (
                <button
                  key={template}
                  onClick={() => setLabel(template)}
                  className="text-left px-3 py-2 text-xs bg-gray-50 hover:bg-teal-50 rounded border border-gray-200 hover:border-teal-200 transition-colors"
                >
                  {template}
                </button>
              ))}
            </div>
          </div>
        </div>

        {/* Footer */}
        <div className="absolute bottom-0 left-0 right-0 p-6 border-t border-gray-200 bg-white">
          <div className="flex items-center justify-between">
            <div className="text-xs text-gray-500">
              Press Ctrl+Enter to save, Esc to cancel
            </div>
            <div className="flex gap-3">
              <Button
                variant="outline"
                onClick={onClose}
                className="px-4"
              >
                Cancel
              </Button>
              <Button
                onClick={handleSave}
                disabled={!label.trim()}
                className="px-4 bg-teal-600 hover:bg-teal-700"
              >
                <Save className="h-4 w-4 mr-2" />
                Save Changes
              </Button>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default EdgeLabelEditSidebar;
