import React, { useState } from 'react';
import { MessageCircle, Settings, Trash2, Globe, Variable } from 'lucide-react';
import { Button } from '@/components/ui/button';



interface VapiNodeProps {
  data: {
    name?: string;
    type: string;
    prompt?: string;
    firstMessage?: string;
    isStart?: boolean;
    variables?: any[];
    globalNodePlan?: { enabled: boolean };
    onUpdate: (data: any) => void;
    onDelete: () => void;
    onConfigure?: () => void;
    onConnectionStart?: (nodeId: string, position: { x: number; y: number }) => void;
  onManualConnectionStart?: (nodeId: string, position: { x: number; y: number }) => void; // New prop for opening side panel
  };
  selected: boolean;
  nodeId?: string; // Add nodeId prop
}

const VapiNode: React.FC<VapiNodeProps> = ({ data, selected, nodeId }) => {
  const [isHovered, setIsHovered] = useState(false);



  const getNodeIcon = (type: string) => {
    switch (type) {
      case 'conversation':
        return {
          icon: <MessageCircle className="h-4 w-4 text-white" />,
          bgColor: 'bg-teal-500'
        };
      case 'apiRequest':
        return {
          icon: <Globe className="h-4 w-4 text-white" />,
          bgColor: 'bg-blue-500'
        };
      case 'transferCall':
        return {
          icon: <Variable className="h-4 w-4 text-white" />,
          bgColor: 'bg-green-500'
        };
      case 'endCall':
        return {
          icon: <Trash2 className="h-4 w-4 text-white" />,
          bgColor: 'bg-red-500'
        };
      case 'tool':
        return {
          icon: <Settings className="h-4 w-4 text-white" />,
          bgColor: 'bg-orange-500'
        };
      default:
        return {
          icon: <MessageCircle className="h-4 w-4 text-white" />,
          bgColor: 'bg-teal-500'
        };
    }
  };

  const getNodeDisplayName = (type: string) => {
    switch (type) {
      case 'conversation':
        return 'Conversation';
      case 'apiRequest':
        return 'API Request';
      case 'transferCall':
        return 'Transfer Call';
      case 'endCall':
        return 'End Call';
      case 'tool':
        return 'Tool';
      default:
        return 'Node';
    }
  };

  return (
    <>
      <div
        className={`
          relative rounded-2xl border-2 shadow-xl transition-all duration-300 min-w-[300px] max-w-[340px] bg-gradient-to-br from-white via-slate-50 to-white backdrop-blur-sm
          ${selected
            ? 'border-indigo-500 shadow-2xl ring-4 ring-indigo-100'
            : 'border-gray-200 hover:border-indigo-300 hover:shadow-lg'
          }
          ${data.isStart
            ? 'ring-4 ring-blue-100 border-blue-400 shadow-blue-100'
            : ''
          }
        `}
        style={{
          minHeight: '140px',
          boxShadow: selected
            ? '0 25px 50px -12px rgba(79, 70, 229, 0.25), 0 0 0 1px rgba(79, 70, 229, 0.05)'
            : '0 10px 25px -5px rgba(0, 0, 0, 0.1), 0 0 0 1px rgba(0, 0, 0, 0.05)',
          transform: selected ? 'scale(1.02)' : 'scale(1)',
        }}
        onMouseEnter={() => setIsHovered(true)}
        onMouseLeave={() => setIsHovered(false)}
      >
        {/* Enterprise Header with gradient background */}
        <div className="flex items-center justify-between p-5 border-b border-gray-100 bg-gradient-to-r from-slate-50 to-gray-50 rounded-t-2xl">
          <div className="flex items-center gap-4">
            <div className={`
              w-10 h-10 rounded-xl flex items-center justify-center shadow-lg transition-all duration-300 hover:scale-110
              ${getNodeIcon(data.type).bgColor}
            `}
            style={{
              boxShadow: '0 4px 12px rgba(0, 0, 0, 0.15), inset 0 1px 0 rgba(255, 255, 255, 0.2)'
            }}>
              {getNodeIcon(data.type).icon}
            </div>
            <div>
              <div className="font-bold text-base text-gray-900 tracking-tight">
                {data.name || getNodeDisplayName(data.type)}
              </div>
              <div className="text-xs text-gray-500 font-medium mt-0.5">
                {data.type.charAt(0).toUpperCase() + data.type.slice(1)} Node
              </div>
            </div>
          </div>

          <div className="flex items-center gap-2">
            <Button
              variant="ghost"
              size="sm"
              className="h-8 w-8 p-0 hover:bg-indigo-50 text-gray-500 hover:text-indigo-600 rounded-lg transition-all duration-200"
            >
              <Globe className="h-4 w-4" />
            </Button>
            <Button
              variant="ghost"
              size="sm"
              className="h-8 w-8 p-0 hover:bg-indigo-50 text-gray-500 hover:text-indigo-600 rounded-lg transition-all duration-200"
            >
              <Variable className="h-4 w-4" />
            </Button>
            {/* Delete Button - Only show for non-start nodes */}
            {!data.isStart && (
              <Button
                variant="ghost"
                size="sm"
                onClick={(e) => {
                  e.stopPropagation();
                  if (window.confirm('Are you sure you want to delete this node?')) {
                    data.onDelete();
                  }
                }}
                className="h-8 w-8 p-0 hover:bg-red-50 text-gray-500 hover:text-red-600 rounded-lg transition-all duration-200"
                title="Delete node"
              >
                <Trash2 className="h-4 w-4" />
              </Button>
            )}
          </div>
        </div>

        {/* Enterprise Content */}
        <div className="p-5 space-y-4">
          <div className="text-sm">
            <div className="font-semibold text-gray-700 mb-3 flex items-center gap-2">
              <div className="w-1 h-4 bg-gradient-to-b from-indigo-500 to-purple-500 rounded-full"></div>
              Prompt Configuration
            </div>
            <div className="text-gray-600 leading-relaxed bg-gray-50 p-3 rounded-lg border border-gray-100">
              {data.prompt && data.prompt.length > 100
                ? `${data.prompt.substring(0, 100)}...`
                : data.prompt || 'No Prompt Specified'}
            </div>
          </div>

          {/* Enterprise Metrics */}
          <div className="flex items-center justify-between text-xs bg-gradient-to-r from-indigo-50 to-purple-50 p-3 rounded-lg border border-indigo-100">
            <div className="flex items-center gap-2">
              <div className="w-2 h-2 bg-green-500 rounded-full animate-pulse"></div>
              <span className="font-medium text-gray-700">Cost: $0.09/min</span>
            </div>
            <div className="flex items-center gap-2">
              <div className="w-2 h-2 bg-blue-500 rounded-full animate-pulse"></div>
              <span className="font-medium text-gray-700">Latency: 1.1s</span>
            </div>
          </div>
        </div>

        {/* Enterprise Top Connection Point (Input) - Perfect positioning */}
        <div
          className="absolute left-1/2 transform -translate-x-1/2 w-4 h-4 bg-gradient-to-br from-indigo-400 to-indigo-600 rounded-full z-20 border-3 border-white cursor-pointer hover:scale-150 hover:from-indigo-500 hover:to-indigo-700 transition-all duration-300 shadow-lg"
          data-connection-point="input"
          data-node-id={nodeId}
          title="Input connection point"
          style={{
            top: '-8px', // Perfect flush positioning
            boxShadow: '0 4px 12px rgba(79, 70, 229, 0.4), inset 0 1px 0 rgba(255, 255, 255, 0.3)'
          }}
        />

        {/* Enterprise Bottom Connection Point (Output) - Perfect positioning */}
        <div
          className="absolute left-1/2 transform -translate-x-1/2 w-4 h-4 bg-gradient-to-br from-purple-400 to-purple-600 rounded-full z-20 border-3 border-white cursor-pointer hover:scale-150 hover:from-purple-500 hover:to-purple-700 transition-all duration-300 shadow-lg"
          data-connection-point="output"
          data-node-id={nodeId}
          title="Output connection point - drag to connect"
          onMouseDown={(e) => {
            e.stopPropagation();
            e.preventDefault();
            console.log('ðŸ”— Starting connection from node:', nodeId);
            if (data.onConnectionStart && nodeId) {
              data.onConnectionStart(nodeId, { x: 0, y: 0 });
            }
          }}
          style={{
            bottom: '-8px', // Perfect flush positioning
            boxShadow: '0 4px 12px rgba(139, 92, 246, 0.4), inset 0 1px 0 rgba(255, 255, 255, 0.3)'
          }}
        />

        {/* Enterprise Hover Add Button with Perfect Spacing */}
        {isHovered && data.type !== 'endCall' && (
          <div className="absolute -bottom-16 left-1/2 transform -translate-x-1/2 z-30">
            <button
              className="w-12 h-12 bg-gradient-to-br from-indigo-500 via-indigo-600 to-purple-600 hover:from-indigo-600 hover:via-indigo-700 hover:to-purple-700 text-white rounded-full flex items-center justify-center transition-all duration-300 shadow-2xl hover:shadow-3xl hover:scale-110 border-2 border-white"
              style={{
                boxShadow: '0 8px 25px rgba(79, 70, 229, 0.4), 0 0 0 4px rgba(255, 255, 255, 0.8), inset 0 1px 0 rgba(255, 255, 255, 0.2)'
              }}
              onClick={(e) => {
                e.stopPropagation();
                if (data.onConnectionStart && nodeId) {
                  data.onConnectionStart(nodeId, { x: 0, y: 0 });
                }
              }}
              title="Add new node"
            >
              <span className="text-xl font-bold leading-none drop-shadow-sm">+</span>

              {/* Subtle pulse ring animation */}
              <div className="absolute inset-0 rounded-full border-2 border-indigo-400 animate-ping opacity-30"></div>
            </button>
          </div>
        )}

      </div>
    </>
  );
};

export default VapiNode;
