import React, { useState, useEffect, useRef } from 'react';
import Vapi from '@vapi-ai/web';
import { Button } from '../../../components/ui/button';
import { Card, CardContent, CardHeader, CardTitle } from '../../../components/ui/card';
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogTrigger } from '../../../components/ui/dialog';
import { Badge } from '../../../components/ui/badge';
import {
  Phone,
  PhoneOff,
  Mic,
  MicOff,
  Volume2,
  Settings,
  Loader2,
  Radio,
  MessageSquare,
  User,
  Bot,
  Pause,
  Play
} from 'lucide-react';
import { toast } from 'sonner';
import secureLocalStorage from 'react-secure-storage';

interface VapiWebCallProps {
  workflowId?: string;
  workflowData?: any;
  workflowName?: string;
  workflowDescription?: string;
  onCallStart?: () => void;
  onCallEnd?: () => void;
  onError?: (error: any) => void;
}

interface CallState {
  isConnected: boolean;
  isConnecting: boolean;
  isMuted: boolean;
  isOnHold: boolean;
  volumeLevel: number;
  callDuration: number;
  status: 'idle' | 'connecting' | 'connected' | 'ended' | 'error';
  callId?: string;
}

interface TranscriptMessage {
  role: 'user' | 'assistant' | 'system';
  content: string;
  timestamp: Date;
  type?: 'transcript' | 'function-call' | 'status';
}

const VapiWebCall: React.FC<VapiWebCallProps> = ({
  workflowId,
  workflowData,
  workflowName = 'Unnamed Workflow',
  workflowDescription = '',
  onCallStart,
  onCallEnd,
  onError
}) => {
  // Get VAPI public key from environment
  const publicApiKey = import.meta.env.VITE_VAPI_PUBLIC_KEY;
  
  // VAPI instance ref
  const vapiRef = useRef<Vapi | null>(null);

  // Media stream ref for mute/hold functionality
  const mediaStreamRef = useRef<MediaStream | null>(null);
  
  // Call state
  const [callState, setCallState] = useState<CallState>({
    isConnected: false,
    isConnecting: false,
    isMuted: false,
    isOnHold: false,
    volumeLevel: 0,
    callDuration: 0,
    status: 'idle',
    callId: undefined
  });
  
  // Transcript and messages
  const [transcript, setTranscript] = useState<TranscriptMessage[]>([]);
  const [assistantId, setAssistantId] = useState<string | null>(null);
  const [isCreatingAssistant, setIsCreatingAssistant] = useState(false);
  
  // Call duration timer
  const callTimerRef = useRef<NodeJS.Timeout | null>(null);

  // Initialize VAPI
  useEffect(() => {
    console.log('ðŸ”§ Initializing VAPI with key:', publicApiKey ? `${publicApiKey.substring(0, 8)}...` : 'Not provided');

    if (publicApiKey && publicApiKey !== 'your-vapi-public-key-here') {
      try {
        vapiRef.current = new Vapi(publicApiKey);
        console.log('âœ… VAPI instance created successfully');

        // Set up event listeners
        setupVapiEventListeners();
        
      } catch (error) {
        console.error('âŒ Failed to initialize VAPI:', error);
        toast.error('Failed to initialize VAPI. Please check your API key.');
      }
    } else {
      console.warn('âš ï¸ VAPI public key not configured');
    }

    return () => {
      if (vapiRef.current) {
        vapiRef.current.stop();
      }
      if (callTimerRef.current) {
        clearInterval(callTimerRef.current);
      }
      // Clean up media stream
      if (mediaStreamRef.current) {
        mediaStreamRef.current.getTracks().forEach(track => track.stop());
        mediaStreamRef.current = null;
      }
    };
  }, [publicApiKey]);

  // Setup VAPI event listeners
  const setupVapiEventListeners = () => {
    if (!vapiRef.current) return;

    // Call start event
    vapiRef.current.on('call-start', () => {
      console.log('ðŸŽ™ï¸ VAPI call started');
      setCallState(prev => ({
        ...prev,
        isConnected: true,
        isConnecting: false,
        status: 'connected'
      }));
      
      // Start call timer
      startCallTimer();
      
      toast.success('Call connected! You can now speak.');
      onCallStart?.();
    });

    // Call end event
    vapiRef.current.on('call-end', () => {
      console.log('ðŸ“ž VAPI call ended successfully');
      setCallState(prev => ({
        ...prev,
        isConnected: false,
        isConnecting: false,
        isMuted: false,
        isOnHold: false,
        status: 'ended',
        callId: undefined
      }));

      // Stop call timer
      if (callTimerRef.current) {
        clearInterval(callTimerRef.current);
        callTimerRef.current = null;
      }

      // Clean up media stream
      if (mediaStreamRef.current) {
        mediaStreamRef.current.getTracks().forEach(track => track.stop());
        mediaStreamRef.current = null;
      }

      toast.success('Call ended successfully');
      onCallEnd?.();
    });

    // Error event
    vapiRef.current.on('error', (error: any) => {
      console.error('âŒ VAPI call error:', error);
      setCallState(prev => ({
        ...prev,
        isConnected: false,
        isConnecting: false,
        isMuted: false,
        isOnHold: false,
        status: 'error',
        callId: undefined
      }));

      // Stop call timer
      if (callTimerRef.current) {
        clearInterval(callTimerRef.current);
        callTimerRef.current = null;
      }

      toast.error(`Call error: ${error.message || 'Unknown error'}`);
      onError?.(error);
    });

    // Speech start event
    vapiRef.current.on('speech-start', () => {
      console.log('ðŸ—£ï¸ Speech started');
    });

    // Speech end event
    vapiRef.current.on('speech-end', () => {
      console.log('ðŸ¤ Speech ended');
    });

    // Volume level event
    vapiRef.current.on('volume-level', (volume: number) => {
      setCallState(prev => ({
        ...prev,
        volumeLevel: volume
      }));
    });

    // Message event (transcripts, function calls, etc.)
    vapiRef.current.on('message', (message: any) => {
      console.log('ðŸ“¨ VAPI message:', message);
      
      if (message.type === 'transcript' && message.transcript) {
        const transcriptMessage: TranscriptMessage = {
          role: message.role || 'assistant',
          content: message.transcript,
          timestamp: new Date(),
          type: 'transcript'
        };
        
        setTranscript(prev => [...prev, transcriptMessage]);
      }
    });

    // Error event
    vapiRef.current.on('error', (error: any) => {
      console.error('âŒ VAPI error:', error);
      setCallState(prev => ({
        ...prev,
        isConnected: false,
        isConnecting: false,
        status: 'error'
      }));
      
      toast.error(`Call error: ${error.message || 'Unknown error'}`);
      onError?.(error);
    });
  };

  // Start call timer
  const startCallTimer = () => {
    callTimerRef.current = setInterval(() => {
      setCallState(prev => ({
        ...prev,
        callDuration: prev.callDuration + 1
      }));
    }, 1000);
  };

  // Format call duration
  const formatDuration = (seconds: number) => {
    const mins = Math.floor(seconds / 60);
    const secs = seconds % 60;
    return `${mins}:${secs.toString().padStart(2, '0')}`;
  };

  // Demo login for testing
  const performDemoLogin = async () => {
    try {
      console.log('ðŸ” Performing demo login...');
      const response = await fetch(`${import.meta.env.VITE_APP_BASE_URL}/api/auth/demo-login`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        }
      });

      if (response.ok) {
        const result = await response.json();
        if (result.success && result.data.token) {
          secureLocalStorage.setItem('token', result.data.token);
          console.log('âœ… Demo login successful');
          toast.success('Demo login successful');
          return result.data.token;
        }
      }
      throw new Error('Demo login failed');
    } catch (error) {
      console.error('âŒ Demo login error:', error);
      toast.error('Demo login failed');
      return null;
    }
  };

  // Create or sync VAPI workflow
  const createVapiWorkflow = async () => {
    try {
      setIsCreatingAssistant(true);
      console.log('ðŸ”„ Creating/syncing VAPI workflow...');

      // Check authentication token
      let token = secureLocalStorage.getItem('token') as string;
      console.log('ðŸ”‘ Auth token:', token ? `${token.substring(0, 20)}...` : 'NOT FOUND');

      if (!token) {
        console.log('ðŸ” No token found, attempting demo login...');
        token = await performDemoLogin();

        if (!token) {
          throw new Error('Authentication failed. Please log in.');
        }
      }

      // Convert workflow data to VAPI workflow format
      const vapiWorkflowData = {
        name: workflowName,
        description: workflowDescription,
        workflowId: workflowId,
        nodes: workflowData?.nodes || [],
        edges: workflowData?.edges || []
      };

      console.log('ðŸ“¤ Sending workflow to VAPI:', `${import.meta.env.VITE_APP_BASE_URL}/api/workflows/vapi/sync`);
      console.log('ðŸ“‹ Workflow data:', vapiWorkflowData);

      const response = await fetch(`${import.meta.env.VITE_APP_BASE_URL}/api/workflows/vapi/sync`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${token}`
        },
        body: JSON.stringify(vapiWorkflowData)
      });

      console.log('ðŸ“¥ Response status:', response.status, response.statusText);

      if (!response.ok) {
        const errorText = await response.text();
        console.error('âŒ API Error Response:', errorText);

        if (response.status === 401) {
          throw new Error('Authentication failed. Please log in again.');
        } else if (response.status === 403) {
          throw new Error('Access denied. You may not have permission to create workflows.');
        } else {
          throw new Error(`Failed to sync workflow: ${response.status} ${response.statusText}`);
        }
      }

      const result = await response.json();
      console.log('ðŸ“¥ API Response:', result);

      if (result.success && result.workflowId) {
        console.log('âœ… VAPI workflow synced:', result.workflowId);
        return result.workflowId;
      } else {
        throw new Error(result.error || 'Failed to sync workflow');
      }

    } catch (error: any) {
      console.error('âŒ Error syncing VAPI workflow:', error);
      toast.error(`Failed to sync workflow: ${error?.message || 'Unknown error'}`);
      return null;
    } finally {
      setIsCreatingAssistant(false);
    }
  };

  // Start web call
  const startWebCall = async () => {
    console.log('ðŸš€ Starting VAPI web call...');

    if (!vapiRef.current) {
      toast.error('VAPI not initialized. Please check your API key configuration.');
      return;
    }

    if (!publicApiKey || publicApiKey === 'your-vapi-public-key-here') {
      toast.error('VAPI public key not configured. Please set VITE_VAPI_PUBLIC_KEY in your environment.');
      return;
    }

    try {
      setCallState(prev => ({
        ...prev,
        isConnecting: true,
        status: 'connecting',
        callDuration: 0
      }));

      setTranscript([]);

      // Sync workflow to VAPI first
      console.log('ðŸ”„ Syncing workflow to VAPI...');
      const vapiWorkflowId = await createVapiWorkflow();

      if (!vapiWorkflowId) {
        throw new Error('Failed to sync workflow to VAPI');
      }

      // Start the call with workflow ID
      console.log('ðŸ“ž Starting workflow call with ID:', vapiWorkflowId);

      // Use VAPI's workflow call format
      const callConfig = {
        workflowId: vapiWorkflowId,
        metadata: {
          portalWorkflowId: workflowId,
          workflowName: workflowName,
          initiatedFrom: 'portal'
        }
      };

      await vapiRef.current.start(callConfig);

    } catch (error) {
      console.error('âŒ Error starting web call:', error);
      setCallState(prev => ({
        ...prev,
        isConnecting: false,
        status: 'error'
      }));
      toast.error(`Failed to start call: ${(error as any)?.message || 'Unknown error'}`);
    }
  };

  // Stop web call
  const stopWebCall = () => {
    console.log('ðŸ›‘ Stopping VAPI web call...');

    try {
      if (vapiRef.current) {
        console.log('ðŸ“ž VAPI instance found, stopping call...');

        // Call the synchronous stop method
        vapiRef.current.stop();

        // The call-end event will handle state cleanup
        // But we can also update state immediately for better UX
        setCallState(prev => ({
          ...prev,
          isConnecting: false
        }));

        console.log('âœ… VAPI stop method called successfully');
      } else {
        console.warn('âš ï¸ No VAPI instance found to stop');
        toast.warning('No active call to end');
      }
    } catch (error) {
      console.error('âŒ Error stopping call:', error);
      toast.error(`Failed to end call: ${(error as any)?.message || 'Unknown error'}`);

      // Force reset state on error
      setCallState(prev => ({
        ...prev,
        isConnected: false,
        isConnecting: false,
        isMuted: false,
        isOnHold: false,
        status: 'ended',
        callId: undefined
      }));

      // Clean up media stream on error
      if (mediaStreamRef.current) {
        mediaStreamRef.current.getTracks().forEach(track => track.stop());
        mediaStreamRef.current = null;
      }
    }
  };

  // Initialize media stream for mute/hold functionality
  const initializeMediaStream = async () => {
    try {
      if (!mediaStreamRef.current) {
        const stream = await navigator.mediaDevices.getUserMedia({ audio: true });
        mediaStreamRef.current = stream;
      }
      return mediaStreamRef.current;
    } catch (error) {
      console.error('Error initializing media stream:', error);
      throw error;
    }
  };

  // Toggle mute - Use VAPI's built-in mute functionality
  const toggleMute = () => {
    try {
      if (!callState.isConnected || !vapiRef.current) {
        toast.warning('No active call to mute');
        return;
      }

      const newMutedState = !callState.isMuted;

      // Use VAPI's built-in setMuted method
      vapiRef.current.setMuted(newMutedState);

      setCallState(prev => ({
        ...prev,
        isMuted: newMutedState
      }));

      toast.info(newMutedState ? 'Microphone muted' : 'Microphone unmuted');
      console.log(`ðŸŽ¤ Mute toggled: ${newMutedState}`);

    } catch (error) {
      console.error('Error toggling mute:', error);
      toast.error(`Failed to toggle mute: ${(error as any)?.message || 'Unknown error'}`);

      // Fallback to media stream control if VAPI method fails
      fallbackToggleMute();
    }
  };

  // Fallback mute method using media stream control
  const fallbackToggleMute = async () => {
    try {
      console.log('ðŸ”„ Using fallback mute method...');
      const stream = await initializeMediaStream();
      const audioTracks = stream.getAudioTracks();

      if (audioTracks.length > 0) {
        const newMutedState = !callState.isMuted;

        // Toggle the audio track enabled state
        audioTracks.forEach(track => {
          track.enabled = !newMutedState;
        });

        setCallState(prev => ({
          ...prev,
          isMuted: newMutedState
        }));

        toast.info(newMutedState ? 'Microphone muted (fallback)' : 'Microphone unmuted (fallback)');
      } else {
        toast.error('No audio tracks found');
      }
    } catch (error) {
      console.error('Fallback mute also failed:', error);
      toast.error('Failed to toggle mute. Please check microphone permissions.');
    }
  };

  // Toggle hold - Pause/resume the call
  const toggleHold = async () => {
    try {
      if (!callState.isConnected) {
        toast.warning('No active call to hold');
        return;
      }

      const newHoldState = !callState.isOnHold;

      // For hold, we'll disable the audio tracks
      const stream = await initializeMediaStream();
      const audioTracks = stream.getAudioTracks();

      audioTracks.forEach(track => {
        track.enabled = !newHoldState; // Disable when on hold, enable when not on hold
      });

      setCallState(prev => ({
        ...prev,
        isOnHold: newHoldState,
        isMuted: newHoldState // When on hold, also show as muted
      }));

      toast.info(newHoldState ? 'Call on hold' : 'Call resumed');
    } catch (error) {
      console.error('Error toggling hold:', error);
      toast.error('Failed to toggle hold. Please check microphone permissions.');
    }
  };

  return (
    <Dialog>
      <DialogTrigger asChild>
        <Button
          variant="outline"
          size="sm"
          className="bg-gradient-to-r from-teal-500 to-cyan-600 hover:from-teal-600 hover:to-cyan-700 text-white border-none shadow-lg hover:shadow-xl transition-all duration-300 relative group overflow-hidden"
        >
          <div className="absolute inset-0 bg-gradient-to-r from-white/20 to-transparent opacity-0 group-hover:opacity-100 transition-opacity duration-300" />
          <Radio className="h-4 w-4 mr-2 relative z-10" />
          <span className="relative z-10">Web Call</span>
          {callState.isConnected && (
            <div className="absolute -top-1 -right-1 w-3 h-3 bg-green-400 rounded-full animate-pulse" />
          )}
        </Button>
      </DialogTrigger>
      
      <DialogContent className="max-w-4xl max-h-[90vh] overflow-hidden" aria-describedby="vapi-web-call-description">
        <DialogHeader>
          <DialogTitle className="flex items-center gap-3">
            <div className="flex items-center gap-2">
              <Radio className="h-5 w-5 text-teal-600" />
              <span>VAPI Web Call</span>
            </div>
            <Badge variant={callState.status === 'connected' ? 'default' : 'secondary'}>
              {callState.status === 'idle' ? 'Ready' : 
               callState.status === 'connecting' ? 'Connecting...' :
               callState.status === 'connected' ? 'Connected' :
               callState.status === 'ended' ? 'Ended' : 'Error'}
            </Badge>
          </DialogTitle>
          <div id="vapi-web-call-description" className="sr-only">
            VAPI Web Call interface for making voice calls with AI assistants
          </div>
        </DialogHeader>
        
        <div className="grid grid-cols-1 lg:grid-cols-2 gap-6 h-[600px]">
          {/* Call Controls Panel */}
          <div className="space-y-4">
            <Card>
              <CardHeader>
                <CardTitle className="text-lg flex items-center gap-2">
                  <Phone className="h-5 w-5 text-teal-600" />
                  Call Controls
                </CardTitle>
              </CardHeader>
              <CardContent className="space-y-4">
                {/* Main Call Button */}
                <div className="flex justify-center">
                  {!callState.isConnected && !callState.isConnecting ? (
                    <Button
                      onClick={startWebCall}
                      size="lg"
                      className="w-20 h-20 rounded-full bg-gradient-to-r from-green-500 to-emerald-600 hover:from-green-600 hover:to-emerald-700 text-white shadow-2xl transition-all duration-300 hover:shadow-3xl hover:scale-110"
                      disabled={isCreatingAssistant}
                    >
                      {isCreatingAssistant ? (
                        <Loader2 className="h-8 w-8 animate-spin" />
                      ) : (
                        <Phone className="h-8 w-8" />
                      )}
                    </Button>
                  ) : callState.isConnecting ? (
                    <Button
                      size="lg"
                      className="w-20 h-20 rounded-full bg-yellow-500 text-white shadow-2xl"
                      disabled
                    >
                      <Loader2 className="h-8 w-8 animate-spin" />
                    </Button>
                  ) : (
                    <Button
                      onClick={stopWebCall}
                      size="lg"
                      className="w-20 h-20 rounded-full bg-gradient-to-r from-red-500 to-red-600 hover:from-red-600 hover:to-red-700 text-white shadow-2xl transition-all duration-300 hover:shadow-3xl hover:scale-110"
                    >
                      <PhoneOff className="h-8 w-8" />
                    </Button>
                  )}
                </div>

                {/* Call Status */}
                <div className="text-center">
                  <div className="text-sm text-gray-600 mb-2">
                    {callState.status === 'idle' && 'Ready to start call'}
                    {callState.status === 'connecting' && 'Connecting to assistant...'}
                    {callState.status === 'connected' && 'Call in progress'}
                    {callState.status === 'ended' && 'Call ended'}
                    {callState.status === 'error' && 'Call error occurred'}
                  </div>
                  
                  {callState.isConnected && (
                    <div className="text-2xl font-mono font-bold text-teal-600">
                      {formatDuration(callState.callDuration)}
                    </div>
                  )}
                </div>

                {/* Control Buttons */}
                {callState.isConnected && (
                  <div className="flex justify-center gap-3">
                    <Button
                      onClick={toggleMute}
                      variant="outline"
                      size="sm"
                      className={callState.isMuted ? 'bg-red-100 border-red-300 text-red-700' : ''}
                      title={callState.isMuted ? 'Unmute microphone' : 'Mute microphone'}
                    >
                      {callState.isMuted ? (
                        <MicOff className="h-4 w-4 mr-2" />
                      ) : (
                        <Mic className="h-4 w-4 mr-2" />
                      )}
                      {callState.isMuted ? 'Unmute' : 'Mute'}
                    </Button>

                    <Button
                      onClick={toggleHold}
                      variant="outline"
                      size="sm"
                      className={callState.isOnHold ? 'bg-yellow-100 border-yellow-300 text-yellow-700' : ''}
                      title={callState.isOnHold ? 'Resume call' : 'Hold call'}
                    >
                      {callState.isOnHold ? (
                        <Play className="h-4 w-4 mr-2" />
                      ) : (
                        <Pause className="h-4 w-4 mr-2" />
                      )}
                      {callState.isOnHold ? 'Resume' : 'Hold'}
                    </Button>

                    <Button
                      variant="outline"
                      size="sm"
                      title="Volume control"
                    >
                      <Volume2 className="h-4 w-4 mr-2" />
                      Volume
                    </Button>
                  </div>
                )}
              </CardContent>
            </Card>

            {/* Call Information */}
            <Card>
              <CardHeader>
                <CardTitle className="text-lg flex items-center gap-2">
                  <Settings className="h-5 w-5 text-blue-600" />
                  Call Information
                </CardTitle>
              </CardHeader>
              <CardContent>
                <div className="space-y-3 text-sm">
                  <div>
                    <span className="text-gray-600">Workflow:</span>
                    <div className="font-medium">{workflowName}</div>
                  </div>
                  <div>
                    <span className="text-gray-600">Workflow ID:</span>
                    <div className="font-mono text-xs bg-gray-100 p-1 rounded mt-1 break-all">
                      {workflowId || 'N/A'}
                    </div>
                  </div>
                  <div>
                    <span className="text-gray-600">Assistant ID:</span>
                    <div className="font-mono text-xs bg-gray-100 p-1 rounded mt-1 break-all">
                      {isCreatingAssistant ? (
                        <span className="flex items-center gap-1">
                          <Loader2 className="w-3 h-3 animate-spin" />
                          Creating assistant...
                        </span>
                      ) : (
                        assistantId || 'Will be created on call start'
                      )}
                    </div>
                  </div>
                  <div>
                    <span className="text-gray-600">API Status:</span>
                    <div className="mt-1">
                      {publicApiKey && publicApiKey !== 'your-vapi-public-key-here' ? (
                        <Badge variant="default" className="bg-green-100 text-green-800">
                          âœ… VAPI Configured
                        </Badge>
                      ) : (
                        <Badge variant="destructive">
                          âŒ API Key Missing
                        </Badge>
                      )}
                    </div>
                  </div>
                </div>
              </CardContent>
            </Card>
          </div>

          {/* Live Transcript Panel */}
          <div className="space-y-4">
            <Card className="h-full">
              <CardHeader>
                <CardTitle className="text-lg flex items-center gap-2">
                  <MessageSquare className="h-5 w-5 text-purple-600" />
                  Live Transcript
                </CardTitle>
              </CardHeader>
              <CardContent className="h-[500px] overflow-y-auto">
                {transcript.length === 0 ? (
                  <div className="flex items-center justify-center h-full text-gray-500">
                    <div className="text-center">
                      <MessageSquare className="h-12 w-12 mx-auto mb-4 opacity-50" />
                      <p>Start a call to see the live transcript</p>
                    </div>
                  </div>
                ) : (
                  <div className="space-y-3">
                    {transcript.map((message, index) => (
                      <div
                        key={index}
                        className={`flex gap-3 ${
                          message.role === 'user' ? 'justify-end' : 'justify-start'
                        }`}
                      >
                        <div
                          className={`max-w-[80%] p-3 rounded-lg ${
                            message.role === 'user'
                              ? 'bg-blue-500 text-white'
                              : 'bg-gray-100 text-gray-900'
                          }`}
                        >
                          <div className="flex items-center gap-2 mb-1">
                            {message.role === 'user' ? (
                              <User className="h-4 w-4" />
                            ) : (
                              <Bot className="h-4 w-4" />
                            )}
                            <span className="text-xs opacity-75">
                              {message.timestamp.toLocaleTimeString()}
                            </span>
                          </div>
                          <p className="text-sm">{message.content}</p>
                        </div>
                      </div>
                    ))}
                  </div>
                )}
              </CardContent>
            </Card>
          </div>
        </div>
      </DialogContent>
    </Dialog>
  );
};

export default VapiWebCall;
